@import '../global/main.scss';

.card{
    border-radius: 10px;
    padding: 12px;
    margin: 8px;

    &_bg-1{
        background-color: $themeColorDark1;
    }

    &_bg-2{
        background-color: $themeColorDark2;
    }

    &_bg-3{
        background-color: $themeColorDark3;
    }

    &_bg-4{
        background-color: $themeColorDark4;
    }

    &_bg-5{
        background-color: $themeColorDark5;
    }

    &_bg-color-1{
        background-color: $themeColor1;
    }

    &_bg-color-2{
        background-color: $themeColor2;
    }

    &_bg-color-3{
        background-color: $themeColor3;
        color: #000000cc;
    }

    &_bg-color-4{
        background-color: $themeColor4;
    }

    &_bg-color-5{
        background-color: $themeColor5;
    }

    &-lesson{
        text-align: center;
    }
}