@import './variables.scss';

body{
    margin: 0px;
    background-color: $themeColorDark5;
}

p{
    margin: 0 0.5rem;
}

.container{
    max-width: 100%;

    &_full{
        margin-left: 1rem;
        margin-right: 1rem;
    }
}

.row{
    display: flex;
    flex-wrap: wrap;

    &_hcenter{
        justify-content: center;
    }

    &_vcenter{
        align-items: center;
    }
}

.col{
    &_auto{
        width: fit-content;
    }

    &_grow{
        flex-grow: 1;
    }
}

.size{
    &_xl{
        font-size: 18px;
    }
}

.h1{
    font-size: 48px;
    font-weight: 600;
    margin: 1rem;
}

.color{
    &_light{
        color: $themeColorDark1;
    }

    &_white{
        color: white;
    }
}

.mb{
    &-2{
        margin-bottom: 1rem;
    }
    
    &-4{
        margin-bottom: 2rem;
    }
}

.border{
    border: 1px solid #333333;

    &_top{
        border-top: 1px solid #333333;
    }

    &_bottom{
        border-bottom: 1px solid #333333;
    }
}